html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 1.6;
}

p {
  color: #000000;
}

.container {
  width: calc(100% - 2rem);
  margin: 0 auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}
a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 1rem;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.25rem;
}

@media (min-width: 768px) {
  h1 {
    font-size: 2.75rem;
  }

  h2 {
    font-size: 2.25rem;
  }

  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 1200px) {
  h1 {
    font-size: 3.25rem;
  }

  h2 {
    font-size: 2.75rem;
  }

  h3 {
    font-size: 2.25rem;
  }
}
.header {
  padding: 3.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 240px;
  margin-bottom: 3.5rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  font-size: 1.125rem;
  font-weight: 700;
  margin-bottom: 1.25rem;
  padding: 0.5rem 1rem;
  width: 100%;
  text-align: center;
  color: #000000;
}
.nav__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .nav__link {
    margin-right: 1.25rem;
    margin-bottom: 0;
    padding: 0;
    width: auto;
  }
  .nav__link:last-of-type {
    margin-right: 0;
  }
}
.nav__link--register {
  background-color: #cf2c2c;
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.nav__link--register:hover {
  cursor: pointer;
  background-color: #a52323;
}
@media (min-width: 768px) {
  .nav__link--register {
    width: auto;
  }
}

.form {
  padding: 3.5rem 0;
}
.form__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2.5rem;
}
@media (min-width: 768px) {
  .form__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.form__content {
  background-color: #cf2c2c;
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 992px) {
  .form__content {
    padding: 3.5rem;
  }
}
.form__heading {
  color: #ffffff;
  text-align: center;
  margin-bottom: 2rem;
}
.form__field {
  margin-bottom: 1.25rem;
}
.form__field:last-of-type {
  margin-bottom: 1.5rem;
}
.form__label {
  display: block;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}
.form__input {
  width: 100%;
  padding: 1rem;
  outline: none;
  border: none;
}
.form__submit {
  background-color: #2f363a;
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 0;
  outline: none;
}
.form__submit:hover {
  cursor: pointer;
  background-color: #181c1e;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .form__submit {
    width: 100%;
  }
}
.form__img-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.form__img {
  width: 250px;
}
@media (min-width: 768px) {
  .form__img {
    width: 100%;
  }
}

.steps__content {
  margin-top: -1px;
  padding: 3.5rem 0;
  background-color: #cf2c2c;
}
.steps__heading {
  color: #ffffff;
  text-align: center;
}
.steps__heading--subtitle {
  margin-top: 0.5rem;
  display: block;
  font-size: 1.125rem;
}
@media (min-width: 768px) {
  .steps__heading--subtitle {
    font-size: 1.375rem;
  }
}
@media (min-width: 1200px) {
  .steps__heading--subtitle {
    font-size: 1.625;
  }
}
.steps__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
}
@media (min-width: 576px) {
  .steps__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .steps__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.steps__img {
  transition: transform 0.3s ease-in-out;
}
.steps__img:hover {
  transform: scale(1.05) rotate(-1deg);
}

.testimonials {
  padding: 3.5rem 0;
}
.testimonials__heading {
  margin-bottom: 3.5rem;
  text-align: center;
}
.testimonials__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
}
@media (min-width: 576px) {
  .testimonials__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .testimonials__grid {
    grid-template-columns: repeat(5, 1fr);
  }
}

.testimonial {
  background-color: #2f363a;
  padding: 1.25rem;
  border-radius: 0.75rem;
  color: #ffffff;
}
@media (min-width: 1200px) {
  .testimonial:nth-child(1) {
    grid-column: 1/4;
    grid-row: 1/2;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
    grid-row: 2/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
    grid-row: 2/3;
  }
}
.testimonial__header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}
.testimonial__img {
  width: 96px;
  margin-right: 1.25rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimonial__author {
  color: #ffffff;
  font-size: 1.125rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.testimonial__rating {
  width: 120px;
}
.testimonial__text {
  padding-left: 1.5rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimonial__text {
    font-size: 1.25rem;
  }
}
.testimonial__text::before {
  content: '"';
  font-size: 80px;
  position: absolute;
  top: -36px;
  left: -5px;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.favorites {
  background-color: #eceeef;
  padding: 3.5rem 0;
}
.favorites__heading {
  text-align: center;
  margin-bottom: 3.5rem;
  color: #2f363a;
}
.favorites__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .favorites__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.favorite {
  background-color: #ffffff;
  padding: 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.favorite__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 992px) {
  .favorite__grid {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
}
.favorite__img {
  width: 120px;
  margin-bottom: 2rem;
}
@media (min-width: 992px) {
  .favorite__img {
    width: 140px;
    margin-bottom: 0;
    margin-right: 2rem;
  }
}
.favorite__content {
  text-align: center;
}
@media (min-width: 992px) {
  .favorite__content {
    text-align: left;
  }
}
.favorite__rating {
  width: 120px;
  margin: 0 auto 1rem;
}
@media (min-width: 992px) {
  .favorite__rating {
    margin: 0 0 1rem;
  }
}
.favorite__name {
  color: #cf2c2c;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .favorite__name {
    font-size: 1.75rem;
  }
}

.deliverymen {
  padding: 3.5rem 0;
}
.deliverymen__heading {
  text-align: center;
  margin-bottom: 3.5rem;
}
.deliverymen__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .deliverymen__grid {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.deliverymen__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
}
.deliverymen__text {
  margin-bottom: 1rem;
}
.deliverymen__btn {
  background-color: #cf2c2c;
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.deliverymen__btn:hover {
  cursor: pointer;
  background-color: #a52323;
}
@media (min-width: 768px) {
  .deliverymen__btn {
    width: auto;
  }
}

.download {
  background-color: #eceeef;
  padding: 3.5rem 0;
}
.download__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3.5rem;
}
@media (min-width: 768px) {
  .download__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
    align-items: center;
  }
}
.download__img {
  width: 220px;
  margin: 0 auto;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 576px) {
  .download__img {
    width: 280px;
  }
}
@media (min-width: 992px) {
  .download__img {
    width: 300px;
  }
}
@media (min-width: 1200px) {
  .download__img {
    width: 320px;
  }
}
.download__img:hover {
  transform: scale(1.05);
}

.stores {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.stores__grid {
  margin-top: 1.5rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
}
@media (min-width: 992px) {
  .stores__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.stores__heading {
  color: #2f363a;
  font-size: 1.75rem;
}
@media (min-width: 768px) {
  .stores__heading {
    font-size: 2rem;
  }
}
@media (min-width: 1200px) {
  .stores__heading {
    font-size: 2.25rem;
  }
}
.stores__subtitle {
  color: #2f363a;
}
.stores__link {
  width: 250px;
  transition: transform 0.3s ease-in-out;
}
.stores__link:hover {
  transform: scale(1.05);
}

.footer {
  background-color: #2f363a;
  padding: 3.5rem 0 0;
}
.footer__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2.5rem;
}
@media (min-width: 576px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.5rem;
  }
}
@media (min-width: 992px) {
  .footer__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__box {
  color: #ffffff;
}
.footer__heading {
  text-align: center;
}
@media (min-width: 576px) {
  .footer__heading {
    text-align: left;
  }
}
.footer__text {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 576px) {
  .footer__text {
    text-align: left;
  }
}
.footer__link {
  display: block;
  margin-bottom: 1rem;
  color: #ffffff;
  text-align: center;
}
@media (min-width: 576px) {
  .footer__link {
    text-align: left;
  }
}
.footer__copyright {
  margin-top: 3.5rem;
  background-color: #cf2c2c;
  color: #ffffff;
  padding: 1.75rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__copyright {
    font-size: 1.125rem;
  }
}

.text-center {
  text-align: center;
}

.pb-0 {
  padding-bottom: 0;
}